/*Variables*/
:root {
  /* primary/main color */
  --clr-primary: hsl(194, 68%, 48%);
  --clr-primary-dark: hsl(194, 67%, 39%);
  --clr-lightGray: rgba(211, 211, 211, 0.308);
  --clr-Gray: rgba(8, 8, 8, 0.623);

  --shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  --shadow-dark: 0 5px 15px rgba(0, 0, 0, 0.2);
  --radius: 8px;

}

/*Global Styles*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  font-family: 'Poppins', sans-serif;
  background: white;
  line-height: 1.5;
  font-size: 0.875rem;
}


h1 {
  font-size: 3rem;
  text-transform: uppercase;
  color: var(--clr-Gray);
}

h2 {
  font-size: 1.6rem;
  ;
}

h3 {
  font-size: 1.3rem;
}

h4 {
  font-size: 0.5rem;
}

body {
  font-size: 1rem;
}

h1,
h2,
h3,
h4 {
  line-height: 1;
}

/*  global classes */

.center_horizontally {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* button style*/

button {
  padding: 0.55rem 0.95rem;
  margin: 0;
  font-size: 1.4rem;
  border-radius: var(--radius);
  border-width: 0;
  box-shadow: var(--shadow);
  cursor: pointer;
  transition: background-color 0.3s ease;
  position: absolute;
  left: 0;
  overflow: hidden;
}

form button {
  position: relative;
  margin-top: 10px;
  font-size: 1.2rem;
}

button:last-child {
  margin-left: 10px;
}

button.primary {
  background-color: var(--clr-primary);
  color: white;
}

button.secondary {
  background-color: rgba(211, 211, 211, 0.568);
  color: black;
}

button.primary:hover {
  background-color: var(--clr-primary-dark);
}

button.secondary:hover {
  background-color: lightgray;
}


/* elements style (id) */

#container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 1.5rem;
}

#warper {
  width: 95%;
  margin: 0 auto;
  margin-top: 2.5rem;
}

#heder {
  padding: 1.5rem 0;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

#selectList {
  width: 50%;
  max-width: 160px;
  min-width: 50px;
  margin: 0;
  font-size: 1.2rem;
  border: none;
  border-radius: var(--radius);
  background-color: var(--clr-lightGray);
  cursor: pointer;
}


/* TodoModal */
#warper_todoModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 10;
  display: grid;
  place-items: center;

}

#warper_addTask {
  background-color: white;
  max-width: 600px;
  width: 90%;
  margin: 0 auto;
  display: grid;
  place-items: center;
  padding: 2rem;
  border-radius: var(--radius);
  position: relative;
}

form#addTask h2 {
  place-items: left;
  color: var(--clr-Gray);
}

form#addTask {
  width: 100%;
  height: 100%;
}


form#addTask input,
select,
textarea,
TextareaAutosize {
  margin-top: 1.5rem;
  height: auto;
  width: 100%;
  padding: 0.8rem;
  border: none;
  background-color: white;
  border: 2px solid var(--clr-primary);
  border-radius: var(--radius);
  font-size: 1.2rem;
  resize: none;
}

form#addTask textarea,
TextareaAutosize {
  min-height: 100px;
}

form#addTask .errorMessage {
  color: red;
}

/* To doo list*/
/* task item */

/* tasks list */
#tasksList {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}


/* Style the list items */

#tasksList li.taskItem {
  box-shadow: var(--shadow-dark);
  padding: 0.55rem 0.85rem;
  border-radius: var(--radius);
  margin-top: 15px;
  overflow: hidden;
  background-color: white;
  cursor: alias;
  transition: background-color 0.1s ease;
  position: relative;
  padding-left: 2rem;

  /* make the list items unselectable */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#tasksList li.taskItem:hover {
  background-color: var(--clr-primary-dark);
}

#tasksList li.taskItem h3 {
  text-decoration: none;
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

#tasksList li.taskItem p {
  white-space: pre-wrap;
  overflow-wrap: break-word;

}

/* task marked as completed*/
div.taskCompleted {
  background-color: var(--clr-primary);
}

.taskItem h3.taskCompleted {
  text-decoration: line-through;
}

/* check box */
.taskItem::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  margin: 0 0.3rem;
  background-color: #fff;
  border: 1.8px solid var(--clr-Gray);
  border-radius: 3px;
}

