/*mobile style*/
@media screen and (max-width: 800px) {
  h1 {
    font-size: 1.8rem;

  }

  h2 {
    font-size: 1.3rem;
  }

  h3 {
    font-size: 1rem;
  }

  h4 {
    font-size: 0.2rem;
  }

  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 1.6rem;
  }

  h3 {
    font-size: 1.3rem;
  }

  h4 {
    font-size: 0.5rem;
  }

  body {
    font-size: 1rem;
  }

  h1,
  h2,
  h3,
  h4 {
    line-height: 1.25;
  }

  button {
    font-size: 1.8rem;
  }

  form button,
  #selectList {
    font-size: 1.6rem;
  }

  #tasksList li.taskItem:hover {
    background-color: initial;
  }
}

